import React from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Home from "./pages/Home/Home";
import Albums from "./pages/Albums/Albums";
import Posts from "./pages/Posts/Posts";
import "./assets/style/all.scss";


function App() {
  return (
    <Router>
      <Routes>
        <Route path="/max-test-react/" element={<Home />} />
        <Route path="/max-test-react/albums" element={<Albums />} />
        <Route path="/max-test-react/albums/:userId" element={<Albums />} />
        <Route path="/max-test-react/albums/album/:albumId" element={<Albums />} />
        <Route path="/max-test-react/posts" element={<Posts />} />
        <Route path="/max-test-react/posts/:userId" element={<Posts />} />
        <Route path="/max-test-react/posts/post/:postId" element={<Posts />} />
      </Routes>
    </Router>
  );
}

export default App;
