import React from "react";
import { Link } from "react-router-dom";
import Logo from "../../assets/images/logo.png";
import "./Header.scss";

const Header = () => {
  return (
    <header className="header">
      <Link to="/max-test-react/" className="logo">
        <img src={Logo} alt="logo" />
      </Link>
      <nav className="nav">
        <Link to="/max-test-react/">Home</Link>
        <Link to="/max-test-react/albums">All albums</Link>
        <Link to="/max-test-react/posts">All posts</Link>
      </nav>
    </header>
  );
}

export default Header;